
.menu-enter-active,
.menu-leave-active {
  transition: all 0.5s;
}
.menu-enter,
.menu-leave-active {
  margin-left: -12.5rem;
}
#app-menu {
  width: 12.5rem;
  min-width: 12.5rem;
  background-color: #324057;
  overflow: auto;
}
#app-menu .logo-wrapper {
  display: block;
  padding-left: 1rem;
  height: 3.5rem;
  line-height: 3.5rem;
  color: #fff;
  font-size: 1.25rem;
  background-color: #435065;
}
#app-menu .el-submenu .iconfont,
#app-menu .el-menu-item .iconfont {
  margin-right: 10px;
}


