
@keyframes shake {
from, to {
    transform: translate3d(0, 0, 0);
}
10%, 30%, 50%, 70%, 90% {
    transform: translate3d(-10px, 0, 0);
}
20%, 40%, 60%, 80% {
    transform: translate3d(10px, 0, 0);
}
}
@keyframes rotate {
0% {
    transform: rotate(0deg) scale(1);
}
50% {
    transform: rotate(180deg) scale(0.6);
}
100% {
    transform: rotate(360deg) scale(1);
}
}
